
.tab-wrap{padding-left:250px;padding-top:10px
}
.tab-wrap ul{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;border-bottom:2px solid #e4e4e4
}
.tab-wrap ul li{background-color:#eee;border-radius:4px 4px 0 0;margin:6px 0 0 6px
}
.tab-wrap ul li:first-child{margin-left:0
}
.tab-wrap ul li a{padding:5px 8px;cursor:pointer
}
.tab-wrap ul li i{width:16px;height:16px;margin-top:-6px;margin-right:-6px;font-size:14px;font-style:normal;font-weight:normal
}
.tab-wrap ul li .close{width:16px;height:16px;margin-top:-4px;margin-right:-8px;vertical-align:middle;text-align:center;color:#666;cursor:pointer;font-style:normal;opacity:1
}
.tab-wrap ul li .close:hover{background-color:red;color:#fff;border-radius:8px
}
.tab-wrap ul li .active{color:#fff
}
.tab-wrap .tab-content{width:100%;height:100%;overflow-y:auto;border:1px solid #dedede;border-top:none;padding:10px 10px
}
.tab-wrap .tab-content .table-responsive{overflow-x:scroll;width:100%;padding-top:8px
}
.tab-wrap thead>tr>th,.tab-wrap tbody>tr>th,.tab-wrap tfoot>tr>th,.tab-wrap thead>tr>td,.tab-wrap tbody>tr>td,.tab-wrap tfoot>tr>td{white-space:nowrap;text-align:center
}
.tab-wrap table{margin-bottom:0
}
.nav-tabs>li.active>a,.nav-tabs>li.active>a:hover,.nav-tabs>li.active>a:focus{background-color:#36adf7;color:#fff;border:none
}
.nav-tabs>li.active{background-color:#36adf7
}



