
.nav-tree{position:relative;display:block;float:left;width:240px;height:100%;margin:0;border:0;padding:0;overflow:visible;background-color:#eef0f1
}
.nav-tree a{color:#666;text-decoration:none
}
.nav-tree .nav-header{position:relative;width:100%;display:block;float:left
}
.nav-tree .nav-header img{display:block;float:left;background-image:url(../../../images/face.png);width:100px;height:100px;display:block;float:left;border-radius:50%
}
.nav-tree .nav-header>div{margin:25px 0;float:left
}
.nav-tree .nav-header .close{position:absolute;top:8px;right:12px;font-weight:300;font-size:17px;color:#808b9c;opacity:1
}
.nav-tree .tree{position:relative;width:100%;clear:both
}
.nav-tree .tree>ul{padding:0
}
.nav-tree .tree>ul>li{list-style-type:none;width:100%;position:relative;padding-left:40px;line-height:40px
}
.nav-tree .tree>ul>li a{display:block;height:100%;width:100%
}
.nav-tree .tree>ul>li i{padding-right:8px
}
.nav-tree .tree .sub-menu{position:relative;padding:0 23px;transition:height 1.1s ease-in-out;display:block
}
.nav-tree .tree .sub-menu li{position:relative;list-style-type:none
}
.nav-tree .tree .sub-menu a{position:relative;display:block;width:100%;height:40px
}
.nav-tree .tree .sub-control{position:absolute;right:9px;top:15px;font-size:10px;color:#959595;transform-origin:center center
}
.hide-sub-menu{height:0
}
.nav-tree .tree .active{background-color:#fafafa
}
.nav-tree .tree .blue-header:before,.nav-tree .tree .hover-header:before{content:"";top:0;bottom:0;left:-63px;right:auto;width:4px;position:absolute;background-color:#1a7ab9
}
.test{height:30px;width:30px;background-color:#000;transition:height 1.1s ease-in-out
}
.slide-left-enter-active,.slide-left-leave-active{transition:all .5s ease-in-out
}
.slide-left-leave-active,.slide-left-enter{transform:translateX(-240px)
}
.slide-up-enter-active,.slide-up-leave-active{transition:all .5 ease-in-out
}
.slide-up-leave-active,.slide-up-enter{height:80px
}



