
.alert-content{width:370px;margin-right:auto;margin-left:auto;border:none;margin-top:100px
}
.alert-content .alert-head{height:34px;line-height:34px;text-align:center;background-color:#36adf7;color:#fff
}
.alert-content .alert-body{text-align:center;vertical-align:middle;padding-top:10px;padding-bottom:60px
}
.alert-content .alert-footer{text-align:center;padding-bottom:10px
}
.alert-content .alert-footer button{background-color:#36adf7;padding:6px 8px;border:1px solid #36adf7;color:#fff;border-radius:4px;cursor:pointer
}
.modal-content{border-radius:0
}



